* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  /* font-family: 'Karla', sans-serif; */
  overflow-x: hidden;

}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
  padding: 1px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(37, 37, 37); 
  border-radius: 2px;
  padding: 1px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
} 

